.home {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: 80vh;
	width: 80vw;
	background-color: white;
	border-color: black;
	border-style: solid;
	border-width: 5px;
}

.home-header {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.home-header-title {
	font-size: 50px;
	font-weight: bold;
	color: rgba(2, 0, 36, 1);
}

.file-uploader {
	display: flex;
	height: 200px;
	width: 500px;
	border: 1px solid black;
	padding: 20px;
	transition: all 0.3s;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin: auto 0;
}

.file-uploader.hover {
	background-color: #f2f2f2;
	border: 1px solid #ccc;
}

.file-uploader:hover {
	transform: scale(1.05);
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.file-uploader h2 {
	margin-top: 0;
}

.file-uploader text {
	text-align: start;
}

.file-uploader-button {
	margin-top: 20px;
	width: 100%;
	height: 40px;
	background-color: #f2f2f2;
	border: 1px solid #ccc;
}

.file-uploader-button:hover {
	background-color: #e6e6e6;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
